*{
    margin: 0;
    padding: 0;
}

/* Header Section */

.navbar{
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #FFFFFF;
    padding: 2% 10%;
}

.navbar-logo{
    display: flex;
    align-items: center;
}

.navbar-main-heading{
    font-family: 'Karla', sans-serif;
    font-size: 25px;
}

.navbar-img{
    width : 50px;
    margin: 0 10px;
}

.navbar-small-heading{
    font-family: 'Inter', sans-serif;
}

/* Body Section */

.meme-form{
    margin: 150px 20% 50px;
}

.meme{
    position: relative;
    margin: 0 20% 50px;
}

.meme-text {
    position: absolute;
    width: 100%;
    text-align: center;
    margin: 15px 0;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.top-text{
    top: 0;
}

.bottom-text{
    bottom: 0;
}

.meme-inputs{
    display: flex;
    justify-content: space-between;
}

.meme-inputs > *{
    padding: 2%;
    width: 40%;
    border-radius: 5px;
    font-family: 'Roboto Slab', serif;
    border: 2px ridge #D5D4D8;
}

.meme-button{
    margin: 5% auto;
    width: 100%;
    padding: 2%;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    border-radius: 5px;
    font-size: 1.1rem;
    border: none;
    color: #FFFFFF;
    cursor: pointer;
    font-family: 'Karla', sans-serif;
} 

.meme-note{
    font-family: 'Karla', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
}

.meme-image{
    width: 100%;
}

@media only screen and (max-width: 550px){
    .navbar{
        padding: 2%;
    }
}